import { FontsLoaderProvider } from '../fonts-loader/fonts-loader';
import { Injectable } from '@angular/core';

import { IcoMoonSelection } from '../../interfaces/interfaces';

/*
  Generated class for the IcoMoonSelectionParserProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class IcoMoonSelectionParserProvider {

  private readonly selectionJsonPath = 'selection.json';
  private readonly versionPath = 'VERSION';

  constructor(
    private loader: FontsLoaderProvider
  ) {
    
  }

  async getLigatures(url: string): Promise<IcoMoonSelection> {
    const version: string = await this.loader.load([url, this.versionPath].filter(Boolean).join('/'));
    let icoMoonSelection: IcoMoonSelection = (await this.loadJson(url) as IcoMoonSelection);
    icoMoonSelection.version = version.replace(/(\r\n|\n|\r)/gm, "");
    icoMoonSelection.ligatureMap = this.getLigaturesMap(icoMoonSelection);
    return icoMoonSelection;
  }

  private getLigaturesMap(icoMoonSelection: IcoMoonSelection) {
    return icoMoonSelection.icons.reduce(function(map, icon) {
      map[icon.properties.name] = icon;
      return map;
    }, {});
  } 

  private loadJson(url: string): Promise<{}> {
    return this.loader.loadJson([url, this.selectionJsonPath].filter(Boolean).join('/'));
  }

}



