import { IcoMoonSelectionParserProvider } from '../ico-moon-selection-parser/ico-moon-selection-parser';
import { Injectable } from '@angular/core';
import { IcoMoonSelection } from '../../interfaces/interfaces';

declare var FontFace; 

/*
  Generated class for the FontsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FontsProvider {

  private readonly BASE_URL: string = 'https://solarwindsmsp-assets-test.swimsp.io/font/';

  constructor(
    private icoMoonSelectionParserProvider: IcoMoonSelectionParserProvider
  ) {
    
  }

  public fetchAssetFonts(version): Promise<IcoMoonSelection> {
    
    return new Promise((resolve, reject) => {
      this.icoMoonSelectionParserProvider.getLigatures(this.BASE_URL+version).then((ligature: IcoMoonSelection)=>{
        this.loadFontFace(version);
        resolve(ligature);
      }).catch((error)=>{
        reject(error)
      })
    });
  }

  private loadFontFace(version): Promise<any> {
    let versionUrl = this.BASE_URL+version;
    return new Promise((resolve, reject) => {
      new FontFace(
        'xuico-'+version, 
        `url('${versionUrl}/fonts/xuico.eot?9r3dyl#iefix') format('embedded-opentype'), url('${versionUrl}/fonts/xuico.woff2?9r3dyl') format('woff2'), url('${versionUrl}/fonts/xuico.ttf?9r3dyl') format('truetype'), url('${versionUrl}/fonts/xuico.woff?9r3dyl') format('woff'), url('${versionUrl}/fonts/xuico.svg?9r3dyl#xuico') format('svg')`,
        { 'font-weight': 'nommal', 'font-style': 'normal' }
      ).load().then(function(loaded_face) {
        (document as any).fonts.add(loaded_face);
      }).then(()=>{
        this.appendStyle(version)
        resolve();
      }).catch((error)=>{
        reject(error);
      })
    });
  }

  private appendStyle(version) {
    var css = `
    [class^="xuico-${version}"], [class*=" xuico-${version}"] {
      /* use !important to prevent issues with browser extensions that change fonts */
      font-family: 'xuico-${version}' !important;
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
  
      /* Enable Ligatures ================ */
      letter-spacing: 0;
      -webkit-font-feature-settings: "liga";
      -moz-font-feature-settings: "liga=1";
      -moz-font-feature-settings: "liga";
      -ms-font-feature-settings: "liga" 1;
      font-feature-settings: "liga";
      -webkit-font-variant-ligatures: discretionary-ligatures;
      font-variant-ligatures: discretionary-ligatures;
  
      /* Better Font Rendering =========== */
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
    `;
    var element = document.createElement('style');
    element.setAttribute('type', 'text/css');

    if ('textContent' in element) {
      element.textContent = css;
    } else {
      (element as any).styleSheet.cssText = css;
    }
    document.getElementsByTagName('head')[0].appendChild(element);
  }

}



