import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FontsProvider } from '../providers/fonts/fonts';
import { HttpClientModule } from '@angular/common/http';
import { FontsLoaderProvider } from '../providers/fonts-loader/fonts-loader';
import { IcoMoonSelectionParserProvider } from '../providers/ico-moon-selection-parser/ico-moon-selection-parser';
import { NgxJsonViewerModule } from 'ngx-json-viewer';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    NgxJsonViewerModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FontsProvider,
    FontsLoaderProvider,
    IcoMoonSelectionParserProvider
  ]
})
export class AppModule {}



