import { Injectable } from '@angular/core';

/*
  Generated class for the FontsLoaderProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FontsLoaderProvider {

  constructor() { }

  public load(url: string): Promise<string> {
    return new Promise((resolve, reject) => {
      const request = new XMLHttpRequest();
      request.open('GET', url);
      request.onload = function() {
        if (request.responseText) {
          resolve(request.responseText);
        } else {
          reject(`Loading ${url} returned nothing (code ${request.status}).`);
        }
      };
      request.send();
    });
  }

  public loadJson(url: string): Promise<{}> {
    return this.load(url).then(data => JSON.parse(data));
  }

}



